import React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useQuery } from '@tanstack/react-query';
import { useFormik } from 'formik';

import Box from '@mui/material/Box';
import Button from '@mui/material/Button';

import { Loader, SelectField, TextField } from '../components';

import { UserQuery } from '../api';
import { useSnackbar } from '../hooks';
import { CreateUser, UserRole, UserRoles } from '../utils/dataTypes';

const initialValues = {
  fullName: '',
  phone: '',
  userRole: 'Admin' as UserRole,
  jobTitle: '',
  email: '',
  password: '',
};

export default function ViewUserForm() {
  const { pathname } = useLocation();
  const navigate = useNavigate();
  const setSnackbarMessage = useSnackbar();

  const {
    isLoading: loading,
    isFetching: fetching,
    data,
  } = useQuery({
    queryKey: ['user', pathname.split('/')[3]],
    queryFn: UserQuery,
    keepPreviousData: false,
    retry: false,
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const formik = useFormik<CreateUser>({
    initialValues: {
      ...initialValues,
      ...data,
    },
    enableReinitialize: true,
    onSubmit: () => {
      return;
    },
  });

  const handleClose = () => {
    navigate(-1);
  };

  const handleEdit = () => {
    navigate(pathname.replace('view', 'edit'), { replace: true });
  };

  return (
    <Box>
      <form onSubmit={formik.handleSubmit} onReset={formik.handleReset}>
        <Box sx={{ p: 3, display: 'grid' }}>
          <Box key="controls" sx={{ display: 'inline-flex' }}>
            <Box key="left" sx={{ p: 1, width: '50%' }}>
              <TextField
                fieldName="fullName"
                label="Full Name"
                disabled
                formik={formik}
              />
              <SelectField
                fieldName="userRole"
                label="User Role"
                items={UserRoles}
                disabled
                formik={formik}
              />
              <TextField
                fieldName="jobTitle"
                label="Job Title"
                disabled
                formik={formik}
              />
            </Box>
            <Box key="right" sx={{ p: 1, width: '50%' }}>
              <TextField
                fieldName="phone"
                label="Phone"
                disabled
                formik={formik}
              />
              <TextField
                fieldName="email"
                label="Email"
                disabled
                formik={formik}
              />
              <TextField
                fieldName="password"
                label="Password"
                disabled
                formik={formik}
              />
            </Box>
          </Box>
          <Box key="buttons" sx={{ display: 'inline-flex' }}>
            <Box key="left" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleEdit}
              >
                Edit
              </Button>
            </Box>
            <Box key="right" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleClose}
              >
                Cancel
              </Button>
            </Box>
          </Box>
        </Box>
      </form>
      <Loader visible={loading || fetching} />
    </Box>
  );
}
